@use 'base/mixins' as m;
@use 'base/variables' as v;

.header{
    background-color: v.$primario;
    height: 8rem;

     a{
        color: v.$blanco;
        font-size: 4rem;
        display: flex;
        justify-content: center;
        padding: 1rem 0;
        &:hover{
            color: pink;
            animation: rotation 1s linear infinite;
        }

        @keyframes rotation {

            from{
                transform: rotateZ(0deg);
            }
            to{
                transform: rotateZ(180deg);
            }
            
        }
    }
}