@use 'variables' as v; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($color, $texto) {
    background-color: $color;
    font-size: 2rem;
    padding: 1.2rem 4rem;
    font-weight: 700;
    display: block;
    width: 100%;
    text-align: center;
    color: $texto;
    text-decoration: none;
    transition: background-color .3s ease;
    border: none;

    &:hover {
        cursor: pointer;
        background-color: v.$secundario;
        color: v.$primario;
    }

    @include tablet {
        width: auto;
       margin: 0 auto;
        
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}


@mixin headingBorder($colorTexto, $colorLinea){
    
    font-size: 3.6rem; 
    display: inline-block;//Corta donde finaliza el contenido 
    color: $colorTexto;
    margin-top: 5rem;//Creara margin en el celular entre la imagen y el h2
    @include tablet { 
        margin-top: 3rem;
    }
        
    &::after{
        content: "";
        background-color: $colorLinea;
        height: 1rem;
        width: 102%;//Crecera un poco mas la linea
        display: block;
        margin: -1.4rem 0 0 0;//Movera el valor a -1
    }
}

@mixin titulo($bgT){
    background-color: $bgT;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 4rem;

    &__t,
    &__numero{
        color: v.$blanco;
        font-size: 1.8rem;
        font-weight: 500;
    }
}