/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: lightgray;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

[class$=heading] {
  font-size: 3.6rem;
  display: inline-block;
  color: #000;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  [class$=heading] {
    margin-top: 3rem;
  }
}
[class$=heading]::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: 102%;
  display: block;
  margin: -1.4rem 0 0 0;
}
[class$=heading] span {
  color: #ee5f00;
  font-weight: 900;
}

a {
  text-decoration: none;
}

img {
  max-width: 30rem;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
  text-align: center;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.header {
  background-color: #0B0B45;
  height: 8rem;
}
.header a {
  color: #FFFFFF;
  font-size: 4rem;
  display: flex;
  justify-content: center;
  padding: 1rem 0;
}
.header a:hover {
  color: pink;
  animation: rotation 1s linear infinite;
}
@keyframes rotation {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(180deg);
  }
}

@media (min-width: 1024px) {
  .cards__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.titulo1 {
  background-color: green;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 4rem;
}
.titulo1__t, .titulo1__numero {
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 500;
}

.titulo2 {
  background-color: #FF6B08;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 4rem;
}
.titulo2__t, .titulo2__numero {
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 500;
}

.card1 {
  border-bottom: 1px solid gainsboro;
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card1:nth-child(2n) {
  font-weight: bold;
}
.card1:nth-child(2n) span {
  color: purple;
}
.card1__check {
  width: 3rem;
  height: 3rem;
  margin-left: 1rem;
}
.card1__star {
  color: yellow;
  padding: 0 2rem;
}
.card1__imagen {
  width: 8rem;
  height: 8rem;
  border-radius: 100%;
}
.card1__fecha {
  background-color: lightblue;
  width: 4rem;
  height: 4rem;
  color: red;
  font-weight: 700;
  text-align: center;
  margin: 0 2rem;
}
.card1__enlace {
  cursor: pointer;
  color: #000;
  text-align: center;
  font-size: 2.3rem;
  font-weight: 700;
  display: block;
  text-align: center;
  padding: 2rem 0;
  background-color: gray;
  margin-bottom: 3rem;
}
.card1__enlace:hover {
  color: #0B0B45;
}
.card1__contenido {
  display: flex;
  line-height: 1.5;
  align-items: center;
  gap: 3rem;
}
.card1__informacion-con {
  display: flex;
}
.card1__informacion {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 1rem;
}

.footer {
  background-color: black;
  padding: 4rem 0;
}
.footer__copy {
  color: #FFFFFF;
  text-align: center;
}

.formulario {
  margin: 5rem 0;
  padding: 2rem;
  background-color: #FFFFFF;
}
.formulario__fieldset {
  border: 3px solid #0B0B45;
}
.formulario__legend {
  color: #FF6B08;
  font-size: 2rem;
  font-weight: 700;
}
.formulario__submit {
  background-color: #0B0B45;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FF6B08;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.formulario__submit:hover {
  cursor: pointer;
  background-color: #FF6B08;
  color: #0B0B45;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
    margin: 0 auto;
  }
}

.campo {
  display: flex;
  flex-direction: column;
}
.campo:last-of-type {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .campo {
    margin-bottom: 3rem;
    flex-direction: row;
    gap: 1rem;
    align-items: center;
  }
}
.campo__label {
  font-weight: 700;
}
.campo__gestor {
  flex: 1;
  padding: 5rem 0;
  border-radius: 0.5rem;
}
.campo.success textarea {
  border: 2px solid green;
}
.campo.error textarea {
  border: 2px solid red;
}
.campo i {
  visibility: hidden;
  top: 4rem;
  right: 1rem;
}
.campo.success i.fa-check-circle {
  color: green;
  visibility: visible;
}
.campo.error i.fa-exclamation-circle {
  color: red;
  visibility: visible;
}
.campo small {
  color: red;
  bottom: 0;
  left: 0;
  visibility: hidden;
}
.campo.error small {
  visibility: visible;
}