@use 'base/mixins' as m;
@use 'base/variables' as v;


.titulo1{
    @include m.titulo(green);

  }

  .titulo2{
    @include m.titulo(v.$secundario);
  }

.card1{
    border-bottom: 1px solid gainsboro;
    background-color: v.$blanco;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &:nth-child(2n){
        font-weight: bold;

        span{
            color: purple;
        }
    }

    &__check{
        width: 3rem;
        height: 3rem;
        margin-left: 1rem;
        
    }

   
    &__star{
        color: yellow;
        padding: 0 2rem;
    }

    &__imagen{
        width: 8rem;
        height: 8rem;
        border-radius: 100%;
    }

    &__fecha{
        background-color: lightblue;
        width: 4rem;
        height: 4rem;
        color: red;
        font-weight: 700;
        text-align: center;
        margin: 0 2rem;
    }

    &__enlace{
        cursor: pointer;
        color: v.$negro;
        text-align: center;
        font-size: 2.3rem;
        font-weight: 700;
        display: block;
        text-align: center;
        padding: 2rem 0;
        background-color: gray;
        margin-bottom: 3rem;
       &:hover{
        color: v.$primario;
        }
    }

    &__contenido{
        display: flex;
        line-height: 1.5;
        align-items: center;
        gap: 3rem;
    }

    &__informacion-con{
        display: flex;
    }
    &__informacion{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 1rem;
    }

}