@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario{
   
    margin: 5rem 0;
    padding: 2rem;
    background-color: v.$blanco;

    &__fieldset{
        border: 3px solid v.$primario;
    }

    &__legend{
        color: v.$secundario;
        font-size: 2rem;
        font-weight: 700;
    }


    &__submit{
        @include m.boton(v.$primario, v.$secundario);

    }
    
}

.campo{
    display: flex;
    flex-direction: column;

    &:last-of-type{
        margin-bottom: 2rem;
    }
     @include m.tablet { 
         margin-bottom: 3rem;
         flex-direction: row;
         gap: 1rem;
         align-items: center;
     }


     &__label{
         
      font-weight: 700;
     }

     &__gestor{
      flex: 1;
      padding: 5rem 0;
      border-radius: .5rem;
        
     }

     &.success textarea{
        border:2px solid green;
     }

     &.error textarea{
        border:2px solid red;
     }


     i{
        visibility: hidden;
        top: 4rem;
        right: 1rem;
     }

     &.success i.fa-check-circle{
        color: green;
        visibility: visible;
     }
     &.error i.fa-exclamation-circle{
        color: red;
        visibility: visible;
     }

     small{
        color: red;
        bottom: 0;
        left: 0;
        visibility: hidden;
     }

     &.error small{
        visibility: visible;
     }

}
